.Main_adminpage {
  height: 100vh;
  width: 100%;
  background-color: rgb(46, 46, 46);
  color: whitesmoke;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid grey;
  text-align: left;
}
.Main_adminpage input{
    height: 40px;
    width: 100%;
    border-radius: 7px;
    margin: 10px 0;
    background-color: transparent;
    color: aliceblue;
}
.Main_adminpage h2{
    margin: 30px;
    font-size: 40px;
    color: aquamarine;

}
.Main_adminpage label{
    font: size 1rem;
    color: aqua;
}
.Main_adminpage button{
    width: 100%;
    height: 50px;
    cursor: pointer;
    background-color: aquamarine;
    border: none;
    border-radius: 8px;
}

