* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
 
}

.Header {
  height: 70px;
  width: 100%;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

#youtube {
  height: 20px;
}

.Headerlogo {
  height: auto;
  width: 230px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}

.Headersignin {
  height: auto;
  width: 200px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;

}
.Header input {
  width: 500px;
  padding: 15px 10px;
  border: 1px solid lightgray;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
}

#Search {
  padding: 14px 20px;
  border: 1px solid lightgray;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  margin-right: 15px;
  background-color: rgb(249, 240, 240);
  font-size: 18px;

}
.Headersearch {
  display: flex;
  align-items: center;
}
span {
  color: rgb(105, 105, 203);
  font-size: 18px;
  text-decoration: none;
  border-bottom: none;
}
.Header i {
  font-size: 1.5rem;
  padding: 10px;

}

i {
  cursor: pointer;
  color: rgb(63, 58, 58);
}

.bar {
  border: none;
  background-color: white;
  
}

.bar:hover {
  border: none;
  background-color: rgba(109, 108, 108, 0.212);
  border-radius: 60%;
 
}




/* <-------media queries start--------> */

@media screen and (max-width:480px){


  /* header start */



html{
  font-size:90%;
 
}

.Header {
  height: auto;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
  padding-top: 10px;
  padding: 6px 30px;
 
}
::-webkit-scrollbar{
  width: 0;
}
#youtube{
  height: 20px;
  margin-bottom: 5px;
  margin-right: 30px;
}
.Headerlogo{
  width: 30%;
}
.Headersignin{
  display: none;
}
.Header input{
  width: 50%;
  padding: 6px 10px;
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
 
}
#Search{
  padding: 5px 7px;
  font-size: 1.2rem;
}
.bar{
  display: none;
}  /* header end*/
  

/* content start */
.Sidebar{
  display: none;
}
.div2{
  display: none;
}
.Contentcenter {
  height: 100%;
  width: 100%;
 
}

.Contentcenter .Minicenter{
  background-color: aqua;
  display: none;

}
.Minicenter a{
  display: none;
}
.Mainvideo{
  height: auto;
  width: 100%;
  margin: 0;
}

/* content end*/

/* first video start */
.video1 .thumbnailimg{
  height: auto;
  width: 90%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
  flex-direction: row;
  margin: 5px 2px;
  aspect-ratio: 16/9;
}
.channellogo {
  height: auto;
  width: 100%;
  border-radius: 50%;
  cursor: pointer;
}
.logoimg {
  height: 40px;
  background-size: cover;
  width: 40px;
  border-radius: 50%;
  margin-right: 5px;
  cursor: pointer;
}
.firstvideo p,
.firstvideo h3 {
  height: auto;
  width: 200px;
}
.thumbnailimg {
  background-repeat: no-repeat;
  background-size: cover;
  height: 130px;
  width: 100%;
  margin: 5px 0;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  transition: all 0.9s;
  
}

.video1 {
  height: auto;
  width: 100%;
  margin: 3px;

}

.ooo {
  height: auto;
  width: 70%;
  display: flex;
  justify-content: center;

}

/* first video end*/

 
}

