.firstvideo {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: start;
  cursor: pointer;
  flex-direction: row;
  margin: 0 2px;

}
.channellogo {
  height: 100%;
  width: 100%;
  border-radius: 50%;
  cursor: pointer;

}

.logoimg {
  height: 40px;
  background-size: cover;
  width: 40px;
  border-radius: 50%;
  margin-right: 5px;
  cursor: pointer;
}

.firstvideo p,
.firstvideo h3 {
  height: auto;
  width: 200px;
}

.firstvideo a {
  text-decoration: none;
  color: grey;
}

.video1 {
  height: auto;
}


.thumbnailimg {
  background-repeat: no-repeat;
  background-size: cover;
  height: 210px;
  width: 315px;
  margin: 10px 5px;
  cursor: pointer;
  border: none;
  border-radius: 12px;
  transition: all 0.9s;

}
.thumbnailimg:hover{
  padding: 10px;
}

#ah3 {
  color: black;
}

#pv {
  color: rgb(18, 20, 22);
}

.ooo {
  height: 100px;
  width: 100%;
  display: flex;
  justify-content: start;
  margin-bottom: 10px;
}