/* sidebar mini first1 start */


.Maincontent {
  height: 100vh;
  width: 100%;
  display: flex;
}

.Maincontent i {
  font-size: 1.2rem;
}

.iconimg {
  height: 30px;
  margin: 10px 0;
}
.Sidebar {
  width:12vw;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
  margin-top: 30px;
  display: inline-block;
  display: none;
 
}
.Sidebar i {
  font-size: 1rem;
  margin: 10px 0;
}
.Sidebar div:hover {
  background-color: lightgray;
  padding: 6px;
  border-radius: 8px;
}
.Sidebar p{
  font-size: 0.8rem;
}
/* sidebar mini first1 end*/

.div2{
  height: 99vh;
  width: 22%;
  overflow-y: scroll;
  background-color: rgb(255, 255, 255);
}
.div2::-webkit-scrollbar{
  width: 10px;
  background-color: rgb(255, 255, 255);
}
.div2::-webkit-scrollbar-thumb{
  height: 40px;
  width: 20px;
  background-color: grey;
}



.div2_innerdives{
  height: 50px;
  width: 100%;
  display: flex;
  justify-content: start;
  align-items: center;
  padding-left: 15px;
  margin-top: 5px;
  border-radius: 6px;
  cursor: pointer;
}
.div2_innerdives:hover{
  background-color: rgb(143, 142, 142);
}
.div2_innerdives p{
  margin-left: 10px;
  font-size: 0.9rem;
}

.div1show{
  display: block;
}
.div2-h3{
  padding: 20px;
}
.div2hide{
  display: none;
}
   /* content center -mini center start */
   
.Contentcenter {
  height: 100%;
  width: 100%;
  margin-left: 20px;
}
.Minicenter{
  height: 70px;
  width:80vw;
   display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  overflow-x: scroll;
  position: relative;
}
.Minicenter::-webkit-scrollbar{
width: 0;
}


.Minicenter a{
  text-decoration: none;
  font-size: 1rem;
  background-color: lightgray;
  color: black;
  margin: 0 5px;
  padding: 10px 20px;
  border-radius: 6px;
  margin-left: 6px;
}

.Minicenter a:hover {
  background-color: black;
  color: white;
  font-weight: 600px;
}


.Mainvideo {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  margin: 10px 10px;
  flex-wrap: wrap;
  overflow-y: scroll;
  scroll-behavior: smooth;
}
.Mainvideo::-webkit-scrollbar{
  width: 0;
  }

#all {
  background-color: black;
  color: white;
}
